@import './functions';

// /.imports

@media screen and (min-width: 375px) {
    .card .flight-time__wrapper {
        grid-template-columns: repeat(4, 1fr);
    }
}

@media screen and (min-width: 525px) {

    // .card .flight-time__wrapper {
    //     grid-template-columns: repeat(7, auto);
    // }

    .card__wrapper {
        justify-content: space-between;
    }

    .card__preview {
        flex-basis: useRem(325);
        flex-shrink: 0;
        max-height: 100%;
        min-height: useRem(370);
        border-radius: useRem(16) 0 0 useRem(16);
    }

    .card__content {
        padding: useRem(22) useRem(34) useRem(32) useRem(12);
    }

    .card__title {
        font-size: useRem(26);
        line-height: useRem(34);
    }

    .card__time {
        font-size: useRem(14);
    }

    .card__preview {
        &::before {
            padding: useRem(12) useRem(34);
            font-size: useRem(16);
            top: useRem(46);
        }
    }

    .card__link {
        font-size: useRem(18);
        padding: useRem(16) useRem(42);
    }

    .description__link {
        font-size: useRem(18);
    }

    .price__value--general {
        font-size: useRem(36);
    }

    .price__value--additional {
        font-size: useRem(14);
    }

    .flight-time__option {
        font-size: useRem(16);
    }
}

@media screen and (min-width: 1024px) {

    .services__list {
        gap: useRem(60);
    }

    // .card .flight-time__wrapper {
    //     grid-template-columns: repeat(4, 1fr);
    // }

    .card__wrapper {
        flex-direction: row;
    }

    .card__title {
        order: -2;
        margin: 0 0 useRem(18) 0;
    }

    .card__duration {
        margin-bottom: useRem(12);
    }

    .card__bottom {
        justify-content: flex-start;
    }

    .card__preview {
        flex-basis: useRem(425);
    }
}

@media screen and (min-width: 1440px) {
    .card__preview {
        flex-basis: useRem(475);
    }
}